import { HttpService } from '../http.service';
import { Component, OnInit } from '@angular/core';

@Component({
  selector: 'na-ideas',
  templateUrl: './ideas.component.html',
  styleUrls: ['./ideas.component.scss']
})
export class IdeasComponent implements OnInit {
  ideaContent: any = "";
  selectedIdea: string = " ";
  ideas: any = {};
  i: any;

  constructor(private http: HttpService) { }

  ngOnInit() {
    this.http.get("assets/data/ideas.json").subscribe(
      data => this.ideas = data,
      error => console.log(error)
    );

    this.i = document.getElementById("idea");
  }

  showIdea(event, idea) {
    event.preventDefault();

    if (this.selectedIdea !== idea.title) {
      // ÐÐ°Ð³ÑÑÐ¶Ð°ÐµÐ¼ ÑÐ°Ð¹Ð» Ð¸Ð´ÐµÐ¸ Ñ ÑÐ°Ð·Ð¼ÐµÑÐºÐ¾Ð¹ Markdown
      this.http.getMarkdown("assets/data/ideas/" + idea.md + ".md").subscribe(
        html => {
          this.ideaContent = `<h2>${idea.title}</h2>` + html; // Ð²ÑÑÐ°Ð²Ð»ÑÐµÐ¼ ÑÐ¾Ð´ÐµÑÐ¶Ð°Ð½Ð¸Ðµ Ð¸Ð´ÐµÐ¸
          this.i.scrollIntoView(); // Ð¿ÐµÑÐµÑÐ¾Ð´Ð¸Ð¼ Ðº ÑÐ¾Ð´ÐµÑÐ¶Ð°Ð½Ð¸Ñ Ð¸Ð´ÐµÐ¸
          this.selectedIdea = idea.title;
        },
        error => console.log(error)
      );
    }
  }
}
